<template>
  <div class="listGoods" :style="[scrollHeight]">
    <nut-scroller
        :is-un-more="isUnMore2"
        :is-loading="isLoading2"
        :type="'vertical'"
        @loadMore="loadMoreVert2"
        @pulldown="pulldown2"
    >
      <div slot="list" class="nut-vert-list-panel">
        <div class="nut-vert-list-item" v-for="n in listData2">
          <div class="nut-scroller-item-info syCell" >
            <img class="syCellImg" src="../../build/logo.png" alt="">
            <div class="syCellContent">
              <div class="syCellContentTitle">
                <div class="syCellContentTitleTag">
                  一口价
                </div>
                吕玲电动车
              </div>
              <div class="syCellContentTip">
                60V20A电池
              </div>
              <div class="syCellContentOriginalPrice">
                <nut-price
                    :price="10010.01"
                    :needSymbol="true"
                    :thousands="false"
                />
              </div>
              <div class="syCellContentPresentPrice">
                <span class="syCellContentPresentPrice">新车价</span>
                <nut-price
                    class="syCellContentPresentPriceVal"
                    :price="0.00"
                    :needSymbol="true"
                    :thousands="false"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </nut-scroller>
  </div>
</template>

<script>
// eslint-disable-next-line no-unused-vars
import router from '@/router'
// eslint-disable-next-line no-unused-vars
import {mapGetters, mapActions} from 'vuex'
export default {
  name: 'ListGoods',
  data () {
    return {
      scrollHeight: {
        height: window.innerHeight - 40 + 'px'
      },
      isUnMore2: false,
      isLoading2: false,
      listData2: [1, 2],
      maxPages: 10,
      page2: 2,
      timers: null,
      count: []
    }
  },
  computed: {},
  mounted () {
    const that = this
    window.onresize = () => {
      return (() => {
        that.scrollHeight = {
          height: this.window.innerHeight - 40 + 'px'
        }
      })()
    }
  },
  methods: {
    loadMoreVert2 () {
      console.log('加载更多')
      this.isLoading2 = true
      if (this.page2 > this.maxPages2) {
        this.isUnMore2 = true
        this.isLoading2 = false
        this.count = this.count + 10
      } else {
        clearTimeout(this.timers)
        this.timer = setTimeout(() => {
          this.isLoading2 = false
          this.isUnMore2 = false
          // this.listData2 = new Array(20 * this.page2)
          this.page2 = ++this.page1
          this.listData2.push(1)
        }, 300)
      }
    },
    pulldown2 () {
      console.log('刷新')
      this.isLoading2 = true
      clearTimeout(this.timers)
      this.timer = setTimeout(() => {
        this.isLoading2 = false
        this.isUnMore2 = false
        this.listData2 = new Array(10)
        this.page2 = 10
        this.count = 10
      }, 300)
    }
  }
}
</script>

<style lang="scss">
  .listGoods{
    .syCell{
      width: 96%;
      height: 6.5rem;
      background-color: #fff;
      text-align: left;
      margin: 0.3rem auto;
      border-radius: 10px;
      padding: 0.5rem 0 0.5rem 0;
      position:relative;
      &Img{
        width: 5rem;
        height: 6.2rem;
        margin-left: 10px;
        vertical-align: middle;
      }
      &Content{
        position: absolute;
        left: 6.5rem;
        top: 0.5rem;
        right: 0.5rem;
        bottom: 0.5rem;
        &Title{
          font-size: 16px;
          padding: 0 0 0.4rem 0;
          &Tag{
            width: 2.3rem;
            height: 1rem;
            padding: 0.1rem 0.2rem 0.1rem 0.2rem;
            color: #fff;
            font-size: 10px;
            display: inline-block;
            background-color: red;
          }
        }
        &Tip{
          color: #8f8f94;
        }
        &OriginalPrice{
          color: red;
          text-align: right;
          padding-right: 10px;
        }
        &PresentPrice{
          text-align: right;
          padding-right: 10px;
          &Val{
            text-decoration: line-through;
          }
        }
      }
    }
  }
</style>



