<template>
  <div class="second-hand-car-tarbar-page">
    <nav-bar></nav-bar>
    <list-goods></list-goods>
  </div>
</template>

<script>
import NavBar from '@/components/NavHeader'
import ListGoods from '@/components/ListGoods'
// eslint-disable-next-line no-unused-vars
import router from '@/router'
// eslint-disable-next-line no-unused-vars
import {mapGetters, mapActions} from 'vuex'
export default {
  name: 'SecondHandCarTarbarPage',
  components: {NavBar, ListGoods}
}
</script>

<style lang="scss">
  .second-hand-car-tarbar-page{
    padding-bottom: 4.5rem;
    padding-top: 2.5rem;
  }
  .syCell{
    width: 96%;
    height: 6.5rem;
    background-color: #fff;
    text-align: left;
    margin: 0.3rem auto;
    border-radius: 10px;
    padding: 0.5rem 0 0.5rem 0;
    position:relative;
    &Img{
      width: 5rem;
      height: 6.2rem;
      margin-left: 10px;
      vertical-align: middle;
    }
    &Content{
      position: absolute;
      left: 6.5rem;
      top: 0.5rem;
      right: 0.5rem;
      bottom: 0.5rem;
      &Title{
        font-size: 16px;
        padding: 0 0 0.4rem 0;
        &Tag{
          width: 2.3rem;
          height: 1rem;
          padding: 0.1rem 0.2rem 0.1rem 0.2rem;
          color: #fff;
          font-size: 10px;
          display: inline-block;
          background-color: red;
        }
      }
      &Tip{
        color: #8f8f94;
      }
      &OriginalPrice{
        color: red;
        text-align: right;
        padding-right: 10px;
      }
      &PresentPrice{
        text-align: right;
        padding-right: 10px;
      }
    }
  }
</style>



