import Vue from 'vue'
import Router from 'vue-router'
import NavHeader from '@/components/NavHeader'
import Home from '@/views/Home'
import HomeTarbarPage from '@/views/HomeTarbarPage'
import PersonTarbarPage from '@/views/PersonTarbarPage'
import SecondHandCarTarbarPage from '@/views/SecondHandCarTarbarPage'
import ListGoods from '@/components/ListGoods'
import PartQuotation from '@/views/PartQuotation'
import BatteryQuote from '@/views/BatteryQuote'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    }, {
      path: '/navHeader',
      name: 'NavHeader',
      component: NavHeader
    }, {
      path: '/listGoods',
      name: 'ListGoods',
      component: ListGoods
    }, {
      path: '/batteryQuote',
      name: 'BatteryQuote',
      component: BatteryQuote
    }, {
      path: '/partQuotation',
      name: 'PartQuotation',
      component: PartQuotation
    }, {
      path: '/home',
      component: Home,
      children: [
        {
          path: 'homeTarbarPage',
          component: HomeTarbarPage
        },
        {
          path: 'personTarbarPage',
          component: PersonTarbarPage
        },
        {
          path: 'secondHandCarTarbarPage',
          component: SecondHandCarTarbarPage
        }
      ]
    }
  ]
})



